import { assetUrl } from './assetResolver.js';

function patchSrcset(srcset) {
  // srcset: "url 1x, url2 2x"
  return String(srcset)
    .split(',')
    .map(part => {
      const trimmed = part.trim();
      if (!trimmed) return trimmed;
      const pieces = trimmed.split(/\s+/);
      const url = pieces[0];
      const descriptor = pieces.slice(1).join(' ');
      const resolved = assetUrl(url);
      return descriptor ? `${resolved} ${descriptor}` : resolved;
    })
    .join(', ');
}

function setHrefSafe(el, value) {
  // For <use> set both href and xlink:href for maximum compatibility
  el.setAttribute('href', value);
  el.setAttribute('xlink:href', value);
}

/**
 * Apply Vite-resolved asset URLs to DOM nodes that use data-src / data-srcset / data-href,
 * and also fixes plain src/srcset/href that still point to ./img/*.
 */
export function applyAssets(root = document) {
  // 1) data-src (img)
  root.querySelectorAll('[data-src]').forEach(el => {
    const raw = el.getAttribute('data-src');
    const resolved = assetUrl(raw);
    el.setAttribute('src', resolved);
    el.removeAttribute('data-src');
  });

  // 2) data-srcset (source/img)
  root.querySelectorAll('[data-srcset]').forEach(el => {
    const raw = el.getAttribute('data-srcset');
    const resolved = patchSrcset(raw);
    el.setAttribute('srcset', resolved);
    el.removeAttribute('data-srcset');
  });

  // 3) data-href (use/link etc)
  root.querySelectorAll('[data-href]').forEach(el => {
    const raw = el.getAttribute('data-href');
    const resolved = assetUrl(raw);

    if (el.tagName.toLowerCase() === 'use') {
      setHrefSafe(el, resolved);
    } else {
      el.setAttribute('href', resolved);
    }
    el.removeAttribute('data-href');
  });

  // 4) Fallback: fix direct src/srcset/href that still references img/*
  root.querySelectorAll('img[src]').forEach(el => {
    const raw = el.getAttribute('src');
    if (!raw) return;
    if (raw.includes('/img/') || raw.startsWith('img/')) {
      el.setAttribute('src', assetUrl(raw));
    }
  });

  root.querySelectorAll('img[srcset], source[srcset]').forEach(el => {
    const raw = el.getAttribute('srcset');
    if (!raw) return;
    if (raw.includes('/img/') || raw.includes('img/')) {
      el.setAttribute('srcset', patchSrcset(raw));
    }
  });

  root.querySelectorAll('use[href], use[xlink\\:href]').forEach(el => {
    const raw = el.getAttribute('href') || el.getAttribute('xlink:href');
    if (!raw) return;
    if (raw.includes('/img/') || raw.startsWith('img/')) {
      setHrefSafe(el, assetUrl(raw));
    }
  });
}

export default applyAssets;
