// src/js/assetResolver.js
// Resolves static assets from /src/img for both:
// - localhost (BASE_URL = '/')
// - GitHub Pages (BASE_URL = '/energy_project/')
// IMPORTANT: keeps "#icon-id" fragments for SVG sprites.

const BASE_URL = (import.meta?.env?.BASE_URL) || '/';

const IMAGES = import.meta.glob('../img/**/*.{png,jpg,jpeg,webp,svg,gif}', {
  eager: true,
  as: 'url',
});

const IMAGE_MAP = new Map();

for (const [key, url] of Object.entries(IMAGES)) {
  // key example: "../img/svg/sprite.svg"
  const normalized = key
    .replace(/^(\.\/|\.\.\/)+/, '') // remove ./ or ../
    .replace(/^img\//, 'img/');     // ensure starts with img/
  IMAGE_MAP.set(normalized, url);
}

// Split "path?query#hash" -> { path, fragment }
function splitPathAndFragment(raw) {
  let s = String(raw || '').trim();
  s = s.replace(/^['"]|['"]$/g, '');

  let fragment = '';
  const hashIndex = s.indexOf('#');
  if (hashIndex !== -1) {
    fragment = s.slice(hashIndex); // keep "#icon-..."
    s = s.slice(0, hashIndex);
  }

  // drop query only from the path part
  const qIndex = s.indexOf('?');
  if (qIndex !== -1) s = s.slice(0, qIndex);

  // decode safe
  try {
    s = decodeURIComponent(s);
  } catch {
    // ignore
  }

  return { path: s, fragment };
}

function normalizeToKey(rawPath) {
  if (!rawPath) return null;

  const { path } = splitPathAndFragment(rawPath);
  let p = path;

  if (p.startsWith('./')) p = p.slice(2);

  const base = BASE_URL.endsWith('/') ? BASE_URL : `${BASE_URL}/`;
  if (p.startsWith(base)) p = p.slice(base.length);

  if (p.startsWith('/')) p = p.slice(1);

  if (!p.startsWith('img/')) {
    const idx = p.indexOf('img/');
    if (idx !== -1) p = p.slice(idx);
  }

  return p.startsWith('img/') ? p : null;
}

export function assetUrl(rawPath) {
  const { fragment } = splitPathAndFragment(rawPath);
  const key = normalizeToKey(rawPath);
  if (!key) return null;

  const url = IMAGE_MAP.get(key);
  if (!url) return null;

  // Keep fragment for sprites: ".../sprite.hash.svg#icon-facebook"
  return `${url}${fragment || ''}`;
}

// Sets url into attribute, and for <use> additionally sets xlink:href.
export function setUrl(el, attrName, rawPath) {
  const url = assetUrl(rawPath);

  if (!url) {
    console.warn(`[setUrl] Not found: ${rawPath}`);
    return false;
  }

  el.setAttribute(attrName, url);

  // For SVG sprites <use> sometimes needs xlink:href too
  if (el.tagName && el.tagName.toLowerCase() === 'use') {
    el.setAttribute('href', url);
    try {
      el.setAttributeNS('http://www.w3.org/1999/xlink', 'xlink:href', url);
    } catch {
      // ignore
    }
  }

  return true;
}

export function resolveSrcset(srcset) {
  if (!srcset) return srcset;

  const parts = String(srcset)
    .split(',')
    .map(s => s.trim())
    .filter(Boolean);

  return parts
    .map(part => {
      const [path, descriptor] = part.split(/\s+/, 2);
      const url = assetUrl(path) || path;
      return descriptor ? `${url} ${descriptor}` : url;
    })
    .join(', ');
}

export function applyAssetUrls(root = document) {
  const scope = root || document;

  scope.querySelectorAll('[data-src]').forEach(el => {
    const raw = el.getAttribute('data-src');
    setUrl(el, 'src', raw);
  });

  scope.querySelectorAll('[data-href]').forEach(el => {
    const raw = el.getAttribute('data-href');
    // for <use> setUrl will set both href and xlink:href
    setUrl(el, 'href', raw);
  });

  scope.querySelectorAll('[data-srcset]').forEach(el => {
    const raw = el.getAttribute('data-srcset');
    el.setAttribute('srcset', resolveSrcset(raw));
  });
}
